.project-info {
    width: 100%;
    padding: 20px 0;
    font-family: 'Roboto Condensed', sans-serif;
    display: -webkit-flex;
    display: flex;
    position: relative;
    /* margin: 10px 20px */
}

.title {
    font-weight: 700;
    margin: auto auto auto 2rem;
    font-size: 24px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.link-button {
    margin: auto 0;
}

.link {
    padding: 10px 10px;
    font-family: inherit;
    background-color: #800080;
    color: white;
    border: 2px white solid;
    border-radius: 5px;
    font-weight: bold;
    font-size: 16px;
    text-decoration: none;
    right: 0;
    width: 120px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: 0.1s ease;
    cursor: pointer;
}

.link:focus {
    outline: none;
}

.link:hover {
    border-color: #800070;
    background-color: white;
    color: black;
}
.project-component{
    display: inline-block;
}

.title {
    display: -webkit-inline-flex;
    display: inline-flex;
}
footer {
    background-color: rgb(31,31,31);
    color: aliceblue;
    padding: 1rem 3rem;
    width: 100%;
    position: fixed;
    margin: 0;
    bottom: 0;
    border-radius: 5px 5px 0 0;
    font-size: 18px;
}

h3 {
    /* font-size: 20px; */
    margin-bottom: 0;
}

.main-footer {
    position: relative;
    bottom: 0;
}

.footer-info {
    margin-left: 1rem;
    margin-top: 1rem;
    width: 55%;
}

.contact-label {
    margin-right: 3rem;
}

.contact {
    margin: auto;
    display: -webkit-inline-flex;
    display: inline-flex;
    position: absolute;
    right: 5%;
    top: 35%;
    width: 30%;
}

.icons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.fab {
    margin: 5px;
}

.contact-icon {
    /* user-select: none; */
    text-decoration: none;
    cursor: pointer;
    color: aliceblue;
    font-size: 1.5rem;
    background-color: transparent;
    border: 2px solid aliceblue;
    height: 3rem;
    width: 3rem;
    margin: 0.6rem;
    border-radius: 50%;
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    transition: 0.4s ease;
}

.contact-icon:hover {
    background-color: #808080;
    color: rgb(31,31,31);
}


.contact-icon,
.contact-icon:link,
.contact-icon:visited,
.contact-icon:hover,
.contact-icon:active {
    text-decoration: none;
}

.fa-html5 {
